<template>
  <div class="my-container" v-cloak>
    <!-- 未登录 -->
    <div class="header not-login" v-if="!user">
      <div class="login-btn" @click="$router.push('/login')">
        <img src="@/assets/mobile.png" class="mobile-img" />
        <span class="text">登录 / 注册</span>
      </div>
    </div>
    <!-- 已登录 -->
    <div class="header user-info" v-else>
      <div class="base-info">
        <div class="left">
          <van-image round :src="avatar" class="avatar" fit="cover" />
          <span class="name">{{ userinfo.name }}</span>
        </div>
        <div class="right">
          <van-button size="mini" round to="/useredit">编辑资料</van-button>
        </div>
      </div>
      <div class="data-stats">
        <div class="data-item">
          <span class="count">{{ userinfo.art_count }}</span>
          <span class="text">头条</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userinfo.follow_count }}</span>
          <span class="text">关注</span>
        </div>
        <div class="data-item">
          <span class="count">{{ userinfo.fans_count }}</span>
          <span class="text">粉丝</span>
        </div>
        <!-- <div class="data-item">
          <span class="count">{{ userinfo.like_count }}</span>
          <span class="text">获赞</span>
        </div> -->
      </div>
    </div>
    <!-- 宫格导航 -->
    <van-grid :column-num="2" clickable>
      <van-grid-item text="收藏">
        <i slot="icon" class="toutiao toutiao-shoucang"></i>
      </van-grid-item>
      <van-grid-item text="历史">
        <i slot="icon" class="toutiao toutiao-lishi"></i>
      </van-grid-item>
    </van-grid>

    <van-cell title="消息通知" is-link />
    <van-cell title="小智同学" is-link class="mb-9" to="/chat" />
    <van-cell title="退出登录" class="exitLogin" clickable v-if="user" @click="exit" />
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getUserInfo } from '@/api/user'
export default {
  name: 'MyIndex',
  data() {
    return {
      // 用户信息
      userinfo: {}
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapState(['avatar'])
  },
  methods: {
    exit() {
      // 退出提示
      this.$dialog
        .confirm({
          title: '是否确认退出'
        })
        .then(() => {
          // 清除登录状态
          this.$store.commit('setUser', null)
        })
        .catch(() => {})
    },
    // 获取用户信息
    async loadUserInfo() {
      try {
        const { data: res } = await getUserInfo()
        this.userinfo = res.data
        this.$store.commit('setAvatar', this.userinfo.photo)
      } catch (err) {
        // this.$toast('获取数据失败，请重新登录或稍后重试')
      }
    }
  },
  created() {
    if (this.user) {
      this.loadUserInfo()
    }
  }
}
</script>

<style lang="less" scoped>
.my-container {
  .header {
    height: 361px;
    background: url('@/assets/banner.png') no-repeat;
    background-size: cover;
  }
  .not-login {
    display: flex;
    justify-content: center;
    align-items: center;
    .login-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      .mobile-img {
        width: 132px;
        height: 132px;
        margin-bottom: 15px;
      }
      .text {
        font-size: 28px;
        color: #fff;
      }
    }
  }
  .user-info {
    .base-info {
      height: 231px;
      padding: 76px 32px 23px;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .avatar {
        width: 132px;
        height: 132px;
        vertical-align: middle;
        margin-right: 23px;
        border: 3px solid #fff;
      }
      .name {
        font-size: 30px;
        color: #fff;
      }
    }
    .data-stats {
      height: 130px;
      display: flex;
      justify-content: space-around;
      .data-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: #fff;
        .count {
          font-size: 36px;
        }
        .text {
          font-size: 23px;
        }
      }
    }
  }

  /deep/.van-grid-item__content--center {
    height: 141px;
    margin-bottom: 9px;
    .toutiao-shoucang,
    .toutiao-lishi {
      font-size: 45px;
      color: #eb5253;
    }
    .toutiao-lishi {
      color: #ff9d1d;
    }
    .van-grid-item__text {
      font-size: 28px;
    }
  }
  .mb-9 {
    margin-bottom: 9px;
  }
  .exitLogin {
    text-align: center;
    color: #d86262;
  }
}
</style>
